___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, sans-serif;
  background: #f4f4f4;
  margin: 0;
  padding: 0;
}
#app {
  max-width: 800px;
  margin: 40px auto;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  padding: 24px;
}
.projects-list {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
}
.project-item {
  padding: 8px 16px;
  border-radius: 4px;
  background: #e0e0e0;
  cursor: pointer;
}
.project-item.selected {
  background: #1976d2;
  color: #fff;
}
